<template>
     <div>
       <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
         <!--  -->
        <el-card>
            <el-row :gutter="20">
                 <el-col :span="6">
                     <el-input placeholder=""  clearable v-model="query"  @clear="getStudentList">
                        <el-button slot="append" icon="el-icon-search" @click="queryStudent"></el-button>
                     </el-input>
                 </el-col>
                 <el-col :span="6">
                        <el-button type="primary" @click="addStudent"></el-button>
                 </el-col>
             </el-row>
              <!--  -->
            <el-table :data="studentList.slice((currentPage-1) * pageSize, currentPage * pageSize)" border stripe style="width: 100%">
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="schoolName"></el-table-column>
                <el-table-column label="" prop="classesName"></el-table-column>
                <el-table-column label="" prop="name"></el-table-column>
                <el-table-column label="" prop="gender"></el-table-column>
                <el-table-column label="" prop="age"></el-table-column>
                <el-table-column label="" prop="height"></el-table-column>
                <el-table-column label="" prop="weight"></el-table-column>
                <el-table-column label="" prop="nature"></el-table-column>
                <el-table-column label="" prop="chairHeight"></el-table-column>
                <el-table-column label="" prop="sittingHeight"></el-table-column>
                <el-table-column label="" prop="correct"></el-table-column>
                <el-table-column label="" prop="description"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showStudentEditDialog(scope.row.id)" ></el-button>
                    </template>
                </el-table-column>
                 <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeStudentsById(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="studentList.length">
            </el-pagination>
              <!--  -->
            <el-dialog title="" :visible.sync="addStudentVisible" width="50%" :before-close="handleClose">
                <el-form :model="addStudentForm" :rules="addStudentRules" ref="studentFormRef" label-width="120px" >
                    <el-form-item label="" prop='stu_cat'>
                        <el-cascader :options="options" v-model="addStudentForm.stu_cat" :props="cateProps" @change="handleChange" clearable></el-cascader>
                    </el-form-item>
                    <el-form-item label="" prop="name">
                        <el-input v-model="addStudentForm.name" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="" prop="gender">
                        <el-radio v-model="addStudentForm.gender" size="medium" border :label="0"></el-radio>
                        <el-radio v-model="addStudentForm.gender" size="medium" border :label="1"></el-radio>
                   </el-form-item>
                   <el-form-item label="" prop="age">
                        <el-input v-model.number="addStudentForm.age" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="()" prop="height">
                        <el-input v-model="addStudentForm.height" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="(KG)" prop="weight">
                        <el-input v-model="addStudentForm.weight" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="()" prop="chairHeight">
                        <el-input v-model="addStudentForm.chairHeight" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="()" prop="sittingHeight">
                        <el-input v-model="addStudentForm.sittingHeight" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="" prop="correct">
                            <el-radio v-model="addStudentForm.correct" size="medium" border  :label="1"></el-radio>
                            <el-radio v-model="addStudentForm.correct" size="medium" border :label="0"></el-radio>
                   </el-form-item>
                   <el-form-item label="">
                        <el-input v-model="addStudentForm.nature" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="" prop="description">
                        <el-input v-model="addStudentForm.description" clearable></el-input>
                   </el-form-item>
                </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="handleClose"> </el-button>
                        <el-button type="primary" @click="sumitAddStudent" > </el-button>
                    </span>
            </el-dialog>
            <!--  -->
             <el-dialog title="" :visible.sync="editStudentVisible" width="50%" >
                <el-form :model="editStudentForm" :rules="editStudentRules" ref="studentEditFormRef" label-width="120px">
                    <el-form-item label="/" prop="name" width="100%">
                        <el-cascader ref="myCascader" :options="options" v-model="selectedOptions" :props="cateProps" @change="handleChange" clearable></el-cascader>
                    </el-form-item>
                    <el-form-item label="" prop="name">
                        <el-input v-model="editStudentForm.name" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="" prop="gender">
                        <el-radio v-model="editStudentForm.gender" size="medium" border :label="0"></el-radio>
                        <el-radio v-model="editStudentForm.gender" size="medium" border :label="1"></el-radio>
                   </el-form-item>
                   <el-form-item label="" prop="age">
                        <el-input v-model.number="editStudentForm.age" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="()" prop="height">
                        <el-input v-model="editStudentForm.height" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="(KG)" prop="weight">
                        <el-input v-model="editStudentForm.weight" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="()" prop="chairHeight">
                        <el-input v-model="editStudentForm.chairHeight" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="()" prop="sittingHeight">
                        <el-input v-model="editStudentForm.sittingHeight" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="" prop="correct">
                            <el-radio v-model="editStudentForm.correct" size="medium" border  :label="1"></el-radio>
                            <el-radio v-model="editStudentForm.correct" size="medium" border :label="0"></el-radio>
                   </el-form-item>
                   <el-form-item label="">
                        <el-input v-model="editStudentForm.nature" clearable></el-input>
                   </el-form-item>
                   <el-form-item label="" prop="description">
                        <el-input v-model="editStudentForm.description" clearable></el-input>
                   </el-form-item>
                </el-form>
                    <span slot="footer" class="dialog-footer">
                      <!--  <el-button @click="reset"> </el-button> -->
                        <el-button type="primary" @click="saveEditInfo" > </el-button>
                    </span>
            </el-dialog>
        </el-card>
     </div>
</template>
<script>
import axios from 'axios'
const thsAreaCode = ''; //  
export default {
    created() {
        this.token = window.sessionStorage.getItem('token');
        this.getOPtions();
        this.getStudentList();
    },
    data() {
        var valiNumberPass1 = (rule, value, callback) => {//
        let reg = /^(([1-9]{1}\d*)|(0{1}))(\.\d{1,2})?$/g;
        if (value === '') {
            callback(new Error(''));
        } else if (!reg.test(value)) {
            callback(new Error(''));
        } else {
            callback();
        }
        };
        return {
            id: '', //id
            query:"",
            token: '',
            options: [],
            addStudentVisible: false,
            schoolId: '',
            classId: '',
            studentList: [],
            currentPage: 1,
            pageSize: 5,
            total:0,
            selectedOptions: [],
            addStudentForm: {
                "age":'' ,
                "chairHeight": '',
                "sittingHeight":'',
                "correct": '',
                "description":"",
                "gender":'',
                "height":'',
                "weight": '',
                "name":"",
                "nature":""

            },
            addStudentRules: {
                name:  { required: true, message: '', trigger: 'blur' },
                age:  { required: true,  type: 'number', message: '', trigger: 'blur' },
                chairHeight:  { required: true, validator: valiNumberPass1, message: '(cm)', trigger: 'blur' },
                sittingHeight:  { required: true,validator: valiNumberPass1, message: '(cm)', trigger: 'blur' },
                correct:  { required: true, type: 'number',message: '', trigger: 'blur' },
                gender:  { required: true,type: 'number', message: '', trigger: 'blur' },
                height:  { required: true, validator: valiNumberPass1, message: '(m)', trigger: 'blur' },
                weight:  { required: true,validator: valiNumberPass1, message: '(kg)', trigger: 'blur' },
                nature:  { required: true, message: '', trigger: 'blur' },
                stu_cat: {required: true, message: '', trigger: 'blur'}
            },
            cateProps: {
               label: 'name', //
               value: 'id', // 
               children: 'children' //
            },
            editStudentVisible: false,
            editStudentForm:{
                "age":'' ,
                "chairHeight": '',
                "sittingHeight":'',
                "correct": '',
                "description":"",
                "gender":'',
                "height":'',
                "weight": '',
                "name":"",
                "nature":"",
                "stu_cat":[]
            },
            editStudentRules: {
                name:  { required: true, message: '', trigger: 'blur' },
                age:  { required: true,  type: 'number', message: '', trigger: 'blur' },
                chairHeight:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                sittingHeight:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                correct:  { required: true, type: 'number',message: '', trigger: 'blur' },
                gender:  { required: true,type: 'number', message: '', trigger: 'blur' },
                height:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                weight:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                nature:  { required: true, message: '', trigger: 'blur' },
                stu_cat: {required: true, message: '', trigger: 'blur'}
            }

        }
    },
     methods: {
       //
         handleClose() {
          this.addStudentVisible = false;
          this.$refs.studentFormRef.resetFields();
          this.addStudentForm.nature = '';
          this.addStudentForm.description = ''
         },
       

        //
        queryStudent() {
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('name', this.query);
            axios({
                method: "post",
                url: '/queryStudent',
                data: param
            }).then(this.handleQuerySucc.bind(this))
            .catch(this.handleQueryErr.bind(this))
        },
        handleQuerySucc(res) {
            if(res.status !== 200) return this.$message.error('');
            this.studentList = res.data.data;
        },
        handleQueryErr(err) {
            console.log(err)
        },
         //
        addStudent() {
            this.addStudentVisible = true
        },
        //
        sumitAddStudent() {
            this.$refs.studentFormRef.validate((valid) => {
                if(!valid) return this.$message.error('');
                let param = new URLSearchParams();
                param.append('token', this.token);
                param.append('schoolId', this.schoolId);
                param.append('classId', this.classId);
                param.append('chairHeight', this.addStudentForm.chairHeight)
                param.append('sittingHeight',this.addStudentForm.sittingHeight)
                param.append('correct', this.addStudentForm.correct)
                param.append('description', this.addStudentForm.description)
                param.append('gender', this.addStudentForm.gender)
                param.append('height', this.addStudentForm.height)
                param.append('weight', this.addStudentForm.weight)
                param.append('name', this.addStudentForm.name)
                param.append('nature', this.addStudentForm.nature)
                param.append('age', this.addStudentForm.age)
                // param.append('stu_cat', this.addStudentForm.stu_cat)
                axios({
                    method: 'post',
                    url: '/addStudent',
                    data: param
                }).then(this.handleAddStuSucc.bind(this)).catch(this.handleAddStuErr.bind(this))
                })
        },
        handleAddStuSucc(res) {
           if(res.status != 200) return this.$message.error('');
           this.addStudentVisible = false;
           this.$message.success('');
           this.$refs.studentFormRef.resetFields();
           this.getStudentList();

        },
        handleAddStuErr(err) {
           console.log(err)
        },
        //
        getOPtions() {
            let param = new URLSearchParams();
            param.append('token', this.token);
            axios({
                method: 'post',
                url: '/cascade1',
                data: param
            }).then(this.handleGetOptionSucc.bind(this)).catch(this.handleGetOptionErr.bind(this))
        },
        handleGetOptionSucc (res) {
            if(res.status !==200) return this.$message.error('');
            this.options =  res.data.data;
        },
        handleGetOptionErr(err) {
            console.log(err)
        },
        //
        handleChange() {
           this.schoolId = this.addStudentForm.stu_cat[0];
           this.classId = this.addStudentForm.stu_cat[1];
        },
        //
        getStudentList() {
            let param = new URLSearchParams();
            param.append('token', this.token);
            axios({
                method: 'post',
                url: '/studentList',
                data: param
            }).then(this.handleGetStudentList.bind(this)).catch(this.handleGetStudentErr.bind(this))
        },
        //
        //pageSize
        handleSizeChange(newSize) {
            this.pageSize = newSize;
            this.getStudentList();
        },
        //
        handleCurrentChange(val) {
           this.currentPage = val;
        },
        handleGetStudentList(res) {
            if(res.status !==200) return this.$message.error('');
            this.studentList = res.data.data;
            // console.log(this.studentList)
            this.studentList.forEach((value, index) => {
                if(value.gender == 1) {
                    value.gender = ''
                }else{
                    value.gender = ''
                }
                if(value.correct ==1 ) {
                    value.correct = ''
                }else {
                    value.correct = ''
                }
            })

        },
        handleGetStudentErr(err) {
            console.log(err)
        },
        // 
        showStudentEditDialog(id) {
            this.id = id;
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('id', id);
            axios({
                method: 'post',
                url: '/editStudent',
                data: param
            }).then(this.handleEditStuSucc.bind(this)).catch(this.handleEditStuErr.bind(this))
        },
        handleEditStuSucc(res) {
            if(res.status !== 200) return;
            if(res.data) {
                this.editStudentForm = res.data.data;
                this.editStudentVisible = true;
                this.selectedOptions.push(res.data.data.schoolId)
                this.selectedOptions.push(res.data.data.classesId)
                this.getStudentList();
            }
        },
        handleAddressFun(e, form, thsAreaCode) {
            thsAreaCode = this.$refs['cascaderAddr'].currentLabels;
            alert(thsAreaCode)

        },
        handleEditStuErr(err) {
            console.log(err)
        },
        //
        saveEditInfo() {
            this.$refs.studentEditFormRef.validate((valid) => {
            if(!valid) return this.$message.error('');
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('schoolId', this.schoolId);
            param.append('classId', this.classId);
            param.append('age', this.editStudentForm.age)
            param.append('chairHeight', this.editStudentForm.chairHeight)
            param.append('sittingHeight',this.editStudentForm.sittingHeight)
            param.append('correct', this.editStudentForm.correct)
            param.append('description', this.editStudentForm.description)
            param.append('gender', this.editStudentForm.gender)
            param.append('height', this.editStudentForm.height)
            param.append('weight', this.editStudentForm.weight)
            param.append('name', this.editStudentForm.name)
            param.append('nature', this.editStudentForm.nature)
            param.append('id', this.id)
            axios({
                method: 'post',
                url: '/saveStudent',
                data: param
            }).then(this.handldEditStuSucc.bind(this)).catch(this.handleEditStuErr.bind(this))
            })
        },
        handldEditStuSucc(res) {
            if(res.status !== 200) return;
             this.studentList = res.data.data;
             //console.log(this.studentList)
            //
            this.editStudentVisible = false;
            //
            this.$message.success('');
            this.getStudentList();
        },
        handleEditStuErr(err) {
            console.log(err)
        },
        //
       async removeStudentsById(id) {
        const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).catch(err => err)
        if(confirmResult !== 'confirm') {
            return this.$message.info('')
        }
            let param = new URLSearchParams();
            param.append('id', id);
            param.append('token', this.token);
            axios({
                method: 'post',
                url: '/deleteStudent',
                data: param
            }).then(this.handleDeleteStuSucc.bind(this)).catch(this.handleDeleteStuErr.bind(this))
        },
        handleDeleteStuSucc(res) {
            if(res.status !== 200) return this.$message.error('');
            this.studentList = res.data.data;
            const totalPage = Math.ceil(this.studentList.length / this.pageSize) // 
            this.currentPage = this.currentPage > totalPage ? totalPage : this.currentPage
            this.currentPage = this.currentPage < 1 ? 1 : this.currentPage
            console.log(this.currentPage)
            // this.getStudentList();
        },
        handleDeleteStuErr(err) {
            console.log(err)
        },
    }
}
</script>
<style lang="less" scoped>

</style>



