<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <el-row :gutter="20">
                <el-col :span="6">
                    <el-input placeholder=""  clearable v-model="query"  @clear="getRecordList">
                    <el-button slot="append" icon="el-icon-search" @click="queryStudent"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                    <el-button type="primary" @click="addRecordDialogVisible = true"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="recordList.slice((currentPage-1) * pageSize, currentPage * pageSize)" border stripe style="width: 100%">
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="schoolName"></el-table-column>
                <el-table-column label="" prop="classesName"></el-table-column>
                <el-table-column label="" prop="studentName"></el-table-column>
                <el-table-column label="" prop="visionLeft"></el-table-column>
                <el-table-column label="" prop="visionRight"></el-table-column>
                <el-table-column label="" prop="eyeAxisLengthLeft"></el-table-column>
                <el-table-column label="" prop="eyeAxisLengthRight"></el-table-column>
                <el-table-column label="" prop="curvatureLeft"></el-table-column>
                <el-table-column label="" prop="curvatureRight"></el-table-column>
                <el-table-column label="" prop="cvaLeft"></el-table-column>
                <el-table-column label="" prop="cvaRight"></el-table-column>
                <el-table-column label="" prop="diopterLeft"></el-table-column>
                <el-table-column label="" prop="diopterRight"></el-table-column>

                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showRecordEditDialog(scope.row.id)" ></el-button>
                    </template>
                </el-table-column>
                 <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeRecordById(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
             <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="recordList.length">
            </el-pagination>
            <!--  -->
            <el-dialog title="" :visible.sync="addRecordDialogVisible" width="50%" :before-close="handleClose">
                <el-form :model="addRecordForm" :rules="addRecordRules" ref="recordFormRef" label-width="120px">
                    <el-form-item label="" prop="record_cat">
                        <el-cascader :options="options" v-model="addRecordForm.record_cat" :props="cateProps" @change="handleChange"clearable></el-cascader>
                    </el-form-item>
                    <el-form-item label="" prop="visionLeft">
                        <el-input v-model="addRecordForm.visionLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" prop="visionRight">
                        <el-input v-model="addRecordForm.visionRight" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="eyeAxisLengthLeft">
                        <el-input v-model="addRecordForm.eyeAxisLengthLeft" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="eyeAxisLengthRight">
                        <el-input v-model="addRecordForm.eyeAxisLengthRight" clearable></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="curvatureLeft">
                        <el-input v-model="addRecordForm.curvatureLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" prop="curvatureRight">
                        <el-input v-model="addRecordForm.curvatureRight" clearable></el-input>
                    </el-form-item>
                    <!--  -->
                     <el-form-item label="" >
                        <el-input v-model="addRecordForm.cvaLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="">
                        <el-input v-model="addRecordForm.cvaRight" clearable></el-input>
                    </el-form-item>
                   <!--  -->
                     <el-form-item label="" >
                        <el-input v-model="addRecordForm.diopterLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="">
                        <el-input v-model="addRecordForm.diopterRight" clearable></el-input>
                    </el-form-item>

                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click=" handleClose"></el-button>
                    <el-button type="primary" @click="submitRecord" > </el-button>
                </span>
            </el-dialog>
            <!--  -->
             <el-dialog title="" :visible.sync="editRecordDialogVisible" width="50%">
                <el-form :model="editRecordForm" :rules="editRecordRules" ref="recordEditFormRef" label-width="120px">
                   <el-form-item label=""  prop="record_cat">
                        <el-cascader :options="options" v-model="record_cat"   props.checkStrictly  :props="cateProps" @change="handleEditChange" clearable></el-cascader>
                    </el-form-item>

                    <el-form-item label=""  prop="sudentName">
                        <el-input v-model="editRecordForm.studentName"   clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="visionLeft">
                        <el-input v-model="editRecordForm.visionLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" prop="visionRight">
                        <el-input v-model="editRecordForm.visionRight" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="eyeAxisLengthLeft">
                        <el-input v-model="editRecordForm.eyeAxisLengthLeft" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="eyeAxisLengthRight">
                        <el-input v-model="editRecordForm.eyeAxisLengthRight" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="curvatureLeft">
                        <el-input  v-model="editRecordForm.curvatureLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" prop="curvatureRight">
                        <el-input  v-model="editRecordForm.curvatureRight" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" >
                        <el-input v-model="editRecordForm.cvaLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="">
                        <el-input  v-model="editRecordForm.cvaRight" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" >
                        <el-input v-model="editRecordForm.diopterLeft" clearable></el-input>
                    </el-form-item>
                     <el-form-item label="" >
                        <el-input v-model="editRecordForm.diopterRight" clearable></el-input>
                    </el-form-item>


                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="editRecordDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="saveEditInfo" > </el-button>
                </span>
            </el-dialog>
        </el-card>
    </div>

</template>
<script>
import axios from 'axios'
var valid
export default {
    created() {
        this.token = window.sessionStorage.getItem('token');
        this.getOPtions();
        this.getRecordList();
    },
    data() {
        var valiNumberPass1 = (rule, value, callback) => {//
            let reg = /^(([1-9]{1}\d*)|(0{1}))(\.\d{1,2})?$/g;
            if (value === '') {
                callback(new Error(''));
            } else if (!reg.test(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        return {
          schoolList: [],
          schoolName: '',
            url: ['../../assets/image/1.jpg'],
            id: '',
            token: '',
            query: '',
            record_cat: [],
            addRecordDialogVisible: false,
            addRecordForm: {
                curvatureLeft: '',
                curvatureRight: '',
                cvaLeft: '',
                cvaRight: '',
                diopterLeft: '',
                diopterRight: '',
                eyeAxisLengthLeft: '',
                eyeAxisLengthRight: '',
                visionLeft: '',
                visionLeft: '',
                record_cat: []
            },
            addRecordRules: {
                record_cat: {required: true,  message: '', trigger: 'blur' },
                curvatureLeft:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                curvatureRight:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                cvaLeft:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                cvaRight:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                diopterLeft:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                diopterRight:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                eyeAxisLengthLeft: { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                eyeAxisLengthRight: { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                visionLeft:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                visionRight:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
            },
            editRecordDialogVisible: false,
            editRecordForm: {
                curvatureLeft: '',
                curvatureRight: '',
                cvaLeft: '',
                cvaRight: '',
                diopterLeft: '',
                diopterRight: '',
                eyeAxisLengthLeft: '',
                eyeAxisLengthRight: '',
                visionLeft: '',
                visionLeft: '',
                record_cat: [],
                studentName: ''
            },
            editRecordRules: {
                record_cat: {required: true,  message: '', trigger: 'blur' },
                curvatureLeft:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                curvatureRight:  { required: true, validator: valiNumberPass1,message: '', trigger: 'blur' },
                cvaLeft:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                cvaRight:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                diopterLeft:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                diopterRight:  { required: true,validator: valiNumberPass1, message: '', trigger: 'blur' },
                eyeAxisLengthLeft:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                eyeAxisLengthRight:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                visionLeft:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                visionRight:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur' },
                studentName:  { required: true, validator: valiNumberPass1, message: '',trigger: 'blur' },
            },
            options: [],
            schoolId: '',
            classId: '',
            studentId: '',
            cateProps: {
               label: 'name', //
               value: 'id', // 
               children: 'children' //
            },
            currentPage: 1,
            pageSize: 5,
            total: 0,
            recordList: []
        }
    },
    methods: {

      //
        handleClose() {
         this.addRecordDialogVisible = false;
         this.$refs.recordFormRef.resetFields();
         this.addRecordForm.cvaLeft = '';
         this.addRecordForm.cvaRight = "";
         this.addRecordForm.diopterLeft = '';
         this.addRecordForm.diopterRight = '';

        },
        //
        queryStudent() {
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('name', this.query);
            axios({
                method: "post",
                url: '/queryRecord',
                data: param
            }).then(this.handleQuerySucc.bind(this))
            .catch(this.handleQueryErr.bind(this))
        },
        handleQuerySucc(res) {
            if(res.status !== 200) return this.$message.error('');
            this.recordList = res.data.data;
        },
        handleQueryErr(err) {
            console.log(err)
        },
        //
        getRecordList() {
            let param = new URLSearchParams();
            param.append('token' ,this.token);
            axios({
                method: 'post',
                data: param,
                url: '/recordList'
            }).then(this.handleGetRecordSucc.bind(this))
            .catch(this.handleGetRecordErr.bind(this)
            )
        },
        handleGetRecordSucc(res) {
            if(res.status !== 200) return this.$message.error('');
            this.recordList = res.data.data;
            this.recordList.forEach((item ,index) => {
              if(item.cvaLeft == 0) {
                 item.cvaLeft = ''
              }
              if(item.cvaRight == 0) {
                 item.cvaRight = ''
              }
              if(item.diopterLeft == '') {
                item.diopterLeft = '--/--'
               }
             if(item.diopterRight == '') {
                item.diopterRight = '--/--'
              }
            })
        },
        handleGetRecordErr(err) {
            console.log(err)
        },
        //
        //pageSize
        handleSizeChange(newSize) {
            this.pageSize = newSize
            this.getRecordList();
        },
        //
        handleCurrentChange(val) {
           this.currentPage = val;
        },
        //
        submitRecord() {
        this.$refs.recordFormRef.validate((valid) => {
            if(!valid) return this.$message.error('');
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('schoolId', this.schoolId);
            param.append('classId', this.classId);
            param.append('studentId', this.studentId);
            param.append('curvatureLeft', this.addRecordForm.curvatureLeft)
            param.append('curvatureRight',this.addRecordForm.curvatureRight)
            param.append('cvaLeft', this.addRecordForm.cvaLeft)
            param.append('cvaRight', this.addRecordForm.cvaRight)
            param.append('diopterLeft', this.addRecordForm.diopterLeft)
            param.append('diopterRight', this.addRecordForm.diopterRight)
            param.append('eyeAxisLengthLeft', this.addRecordForm.eyeAxisLengthLeft)
            param.append('eyeAxisLengthRight', this.addRecordForm.eyeAxisLengthRight)
            param.append('visionLeft', this.addRecordForm.visionLeft)
            param.append('visionRight', this.addRecordForm.visionRight)
            axios({
                method: 'post',
                url: '/addRecord',
                data: param
            }).then(this.handleAddRecordSucc.bind(this)).catch(this.handleAddRecordErr.bind(this))
            })
        },
        handleAddRecordSucc(res) {
            if(res.status != 200) return this.$message.error('');
            this.addRecordDialogVisible = false;
            this.$message.success('');
            this.$refs.recordFormRef.resetFields();
            this.addRecordForm.cvaLeft = '';
            this.addRecordForm.cvaRight = "";
            this.addRecordForm.diopterLeft = '';
            this.addRecordForm.diopterRight = '';
            this.getRecordList();

        },
        handleAddRecordErr(err) {
          this.$message.error('')
            console.log(err)
        },
        handleChange() {
            this.schoolId = this.addRecordForm.record_cat[0];
            this.classId = this.addRecordForm.record_cat[1];
            this.studentId = this.addRecordForm.record_cat[2];
            console.log(this.schoolId, this.classId);
        },
        handleEditChange() {
          this.schoolId = this.editRecordForm.record_cat[0];
          this.classId = this.editRecordForm.record_cat[1];
          this.studentId = this.editRecordForm.record_cat[2];
        },
        //
        showRecordEditDialog(id) {
            this.id = id;
            let param = new URLSearchParams();
            param.append('id', id);
            param.append('token',this.token)
            axios({
                method: 'post',
                url: '/editRecord',
                data: param
            }).then(this.handleEditRecordSucc.bind(this))
            .catch(this.handleEditRecordErr.bind(this))
            },
        handleEditRecordSucc(res) {
          if(res.status !== 200) return;
          res ? res = res.data: '';
            this.editRecordForm = res.data;
            this.record_cat = res.data.record_cat;

           console.log(this.record_cat)
          this.editRecordDialogVisible = true;
          },
        handleEditRecordErr(err) {
          this.$message.error('');
            console.log(err)
        },

        //
        saveEditInfo() {
            this.$refs.recordEditFormRef.validate((valid) => {
            if(!valid) return this.$message.error('');
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('schoolId', this.schoolId);
            param.append('classId', this.classId);
            param.append('studentId', this.studentId);
            param.append('curvatureLeft', this.editRecordForm.curvatureLeft)
            param.append('curvatureRight',this.editRecordForm.curvatureRight)
            param.append('cvaLeft', this.editRecordForm.cvaLeft)
            param.append('cvaRight', this.editRecordForm.cvaRight)
            param.append('diopterLeft', this.editRecordForm.diopterLeft)
            param.append('diopterRight', this.editRecordForm.diopterRight)
            param.append('eyeAxisLengthLeft', this.editRecordForm.eyeAxisLengthLeft)
            param.append('eyeAxisLengthRight', this.editRecordForm.eyeAxisLengthRight)
            param.append('visionLeft', this.editRecordForm.visionLeft)
            param.append('visionRight', this.editRecordForm.visionRight)
            param.append('id', this.id);
            axios({
                method: 'post',
                url: '/saveRecord',
                data: param
            }).then(this.handleSaveEditSucc.bind(this)).catch(this.handleSaveEditErr.bind(this))
            })
        },
    handleSaveEditSucc(res) {
        if(res.status != 200) return this.$message.error('');
        console.log(res)
        this.editRecordDialogVisible = false;
        this.$refs.recordEditFormRef.resetFields();
        this.$message.success('');
        this.getRecordList();

    },
    handleSaveEditErr(err) {
        console.log(err)
    },
    //
    async removeRecordById(id) {
        const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        }).catch(err => err)
        if(confirmResult !== 'confirm') {
            return this.$message.info('')
        }
        let param = new URLSearchParams();
        param.append('token', this.token);
        param.append('id', id)
        axios({
            method: 'post',
            url: '/deleteRecord',
            data: param
        }).then(this.handleDeleteRecordSucc.bind(this))
        .catch(this.handleDeleteRecordErr.bind(this))
    },
    handleDeleteRecordSucc(res) {
        if(res.status !== 200) return this.$message.error('');
        this.$message.success('');
        this.recordList = res.data.data;
        const totalPage = Math.ceil(this.recordList.length / this.pageSize) // 
        this.currentPage = this.currentPage > totalPage ? totalPage : this.currentPage
        this.currentPage = this.currentPage < 1 ? 1 : this.currentPage
    },
    handleDeleteRecordErr(err) {
        console.log(err)
    },
    //
    getOPtions() {
        let param = new URLSearchParams();
        param.append('token', this.token);
        axios({
            method: 'post',
            url: '/cascade1',
            data: param
        }).then(this.handleGetOptionSucc.bind(this)).catch(this.handleGetOptionErr.bind(this))
    },
    handleGetOptionSucc (res) {
        if(res.status !==200) return this.$message.error('');
        this.options = res.data.data;
    },
    handleGetOptionErr(err) {
        console.log(err)
    }
}
}

</script>
<style lang="less" scoped>

</style>



